//http.js
import {
  config
} from './config.js'
import message from './message.js'

var http = {
  post(path, params, contentType = 'json', otherUrl, ) {
    return new Promise((resolve, reject) => {
      var url = (otherUrl || config.baseUrl) + path
      if (!checkUrl(url)) {
        rej('')
      }
      uni.request({
        method: 'POST',
        url,
        header: {
          "Content-Type": contentType === 'json' ? "application/json" : "application/x-www-form-urlencoded",
          token: uni.getStorageSync("token")
        },
        data: params,
        success: (res) => {
          if (res.statusCode == 200 && res.data.code == 200) {
            let data = res.data
            if (data.hasOwnProperty("token")) {
              let token = data.token
              // console.log(token)
              uni.setStorageSync("token", token)
            }
            console.log('request:POST' + config.baseUrl + path + ' ', res.data)
          }

          resolve(res.data)
        },
        fail: (err) => {
          message.toast('', 'err')
          console.error('request:' + config.baseUrl + path + ' ', err)
          reject('')
        }
      })
    })
  },
  put(path, params, contentType = 'json', otherUrl, ) {
    return new Promise((resolve, reject) => {
      var url = (otherUrl || config.baseUrl) + path
      if (!checkUrl(url)) {
        rej('')
      }
      uni.request({
        method: 'PUT',
        url,
        header: {
          "Content-Type": contentType === 'json' ? "application/json" : "application/x-www-form-urlencoded",
          token: uni.getStorageSync("token")
        },
        data: params,
        success: (res) => {
          if (res.statusCode == 200 && res.data.code == 200) {
            let data = res.data
            if (data.hasOwnProperty("token")) {
              let token = data.token
              // console.log(token)
              uni.setStorageSync("token", token)
            }
            console.log('request:PUT' + config.baseUrl + path + ' ', res.data)
          }
          resolve(res.data)
        },
        fail: (err) => {
          message.toast('', 'err')
          console.error('request:PUT' + config.baseUrl + path + ' ', err)
          reject('')
        }
      })
    })
  },

  get(path, params, otherUrl) {
    return new Promise((resolve, reject) => {
      var url = (otherUrl || config.baseUrl) + path
      if (!checkUrl(url)) {
        return
      }
      uni.request({
        url,
        data: params,
        header: {
          token: uni.getStorageSync("token")
        },
        success: (res) => {
          if (res.statusCode == 200 && res.data.code == 200) {
            let data = res.data
            if (data.hasOwnProperty("token")) {
              let token = data.token
              // console.log(token)
              uni.setStorageSync("token", token)
            }
            fun(res)
            console.log('request:GET' + config.baseUrl + path + ' ', res.data)
          }
          resolve(res.data)
        },
        fail: (err) => {
          message.toast('', 'err')
          console.error('request:GET' + config.baseUrl + path + ' ', err)
          reject(err)
        }
      })

    })

  },
  delete(path, params, otherUrl) {
    return new Promise((resolve, reject) => {
      var url = (otherUrl || config.baseUrl) + path
      if (!checkUrl(url)) {
        return
      }
      uni.request({
        url,
        data: params,
        method: "DELETE",
        header: {
          token: uni.getStorageSync("token")
        },
        success: (res) => {
          if (res.statusCode == 200 && res.data.code == 200) {
            let data = res.data
            if (data.hasOwnProperty("token")) {
              let token = data.token
              // console.log(token)
              uni.setStorageSync("token", token)
            }
            
            console.log('request:DELETE' + config.baseUrl + path + ' ', res.data)
          }
          resolve(res.data)
        },
        fail: (err) => {
          message.toast('', 'err')
          console.error('request:DELETE' + config.baseUrl + path + ' ', err)
          reject(err)
        }
      })

    })

  },

  async upload(path, fileArray, otherUrl) {

    if (typeof fileArray !== 'object') {
      console.error('request:,')
      return
    }
    var url = (otherUrl || config.baseUrl) + path
    if (!checkUrl(url)) {
      return
    }
    var arr = []
    for (let i in fileArray) {
      const res = await uni.uploadFile({
        url: otherUrl || config.baseUrl + path,
        filePath: fileArray[i],
        name: 'file'
      })
      console.log(res)
      if (res[0]) {
        console.error('request:', res[0])
        return
      }
      arr.push(JSON.parse(res[1].data).data)
    }
    return arr
  },

}

function checkUrl(url) {
  var urlReg = /^((ht|f)tps?):\/\/[\w\-]+(\.[\w\-]+)+([\w\-.,@?^=%&:\/~+#]*[\w\-@?^=%&\/~+#])?$/;
  if (!urlReg.test(url)) {
    console.error('request:' + url)
    return false
  }
  return true
}
export default http
